"use strict";
var OtsuBinary = document.getElementById('OtsuBinary');
OtsuBinary.addEventListener('click', function () {
    var canvas = document.getElementById('canvas4');
    if (!canvas) {
        console.log('Canvas要素の取得に失敗');
        return;
    }
    var context = canvas.getContext('2d');
    var image = new Image();
    image.src = "../images/lenna.jpeg";
    image.onload = function () {
        context.drawImage(image, 0, 0);
        var imageData = context.getImageData(0, 0, image.width, image.height);
        var width = imageData.width;
        var height = imageData.height;
        var length = imageData.data.length;
        var pixel = imageData.data;
        var hist = new Array(256);
        for (var i = 0; i < 256; i++)
            hist[i] = 0;
        for (var i = 0; i < length; i += 4) {
            var r = pixel[i + 0];
            var g = pixel[i + 1];
            var b = pixel[i + 2];
            var gray = Math.round(0.30 * r + 0.59 * g + 0.11 * b);
            pixel[i + 0] = gray;
            pixel[i + 1] = gray;
            pixel[i + 2] = gray;
            pixel[i + 3] = pixel[i + 3];
            hist[gray] += 1;
        }
        var thresh = getThreshold(hist, 256);
        //const thresh = 120;
        for (var i = 0; i < length; i += 4) {
            var r = pixel[i + 0];
            var binary = void 0;
            if (r <= thresh) {
                binary = 255;
            }
            else {
                binary = 0;
            }
            pixel[i + 0] = binary;
            pixel[i + 1] = binary;
            pixel[i + 2] = binary;
            pixel[i + 3] = pixel[i + 3];
        }
        context.putImageData(imageData, 0, 0);
    };
}, false);
var getThreshold = function (hist, n) {
    var max = (1000.0);
    var thresh = 0;
    var sa, sb, da, db;
    var ma, mb, mt;
    var wa, wb, wt;
    var kai;
    for (var i = 1; i < n - 1; i++) {
        da = 0;
        sa = 0;
        for (var j = 0; j < i; j++) {
            da += (hist[j] * j);
            sa += hist[j];
        }
        db = 0;
        sb = 0;
        for (var j = i; j < n; j++) {
            db += (hist[j] * j);
            sb += hist[j];
        }
        if (sa != 0.0) {
            ma = da / sa;
        }
        else {
            ma = 0.0;
        }
        if (sb != 0.0) {
            mb = db / sb;
        }
        else {
            mb = 0.0;
        }
        mt = (da + db) / (sa + sb);
        wa = sa / (sa + sb);
        wb = sb / (sa + sb);
        kai = wa * (ma - mt) * (ma - mt) + (wb * (mb - mt) * (mb - mt));
        if (max < kai) {
            max = kai;
            thresh = i;
        }
    }
    return thresh;
};
